import None from '../../public/noNumber'
import '../../../static/css/zan/iconfont.css'
import Head from '../../public/head'
import Loading from '../../public/Loading'
import test from '../../../static/js/language'
  export default {
      name: "translation",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          colorIndex:'0',
          list:[],
          transList:[],
          activeId:'',
          languageId:0,
          isLoading:false,
          owerId:'',
          total:0,
          index:1
        }
    },
    components:{
        Head,
      Loading,
      None
    },
    watch:{
      languageId:function(){
        this.gettrans(this.languageId,1)
      }
    },
    methods:{
      handleCurrentChange(val){
        this.gettrans(this.languageId,val)
      },
      changeRouter(item,index){
        if(index != this.colorIndex){
          this.colorIndex = index
          this.languageId = item.value
        }
      },
      goBack(){
        this.$router.go(-1)
      },
      gettrans(val,page){
        this.isLoading = true
        this.$http.post(this.api + '/TranslateL',{
          token: localStorage.getItem('token'),
          activity_id: this.activeId,
          language: val,
          sort: 2,
          page: page
        })
          .then(res=>{
            if(res.data.code == 1){
              this.transList = res.data.data.data
              this.total = res.data.data.total
              this.isLoading = false
            }else if(res.data.code == 3){

            }else{
              alert(res.data.msg)
            }
          })
      },
      addPrase(item){
        this.$http.post(this.api + '/home/Comment/praise',{
          token: localStorage.getItem('token'),
          flag: 2,
          table_id: item.translate_id,
          type: item.is_praise? 2:1
        })
          .then(res=>{
            if(res.data.code == 1){
              if(item.is_praise){
                item.praise_num -= 1
                this.$message({
                  type:'success',
                  message:'取消点赞！'
                })
              }else{
                item.praise_num += 1
                this.$message({
                  type:'success',
                  message:'点赞成功！'
                })
              }
              item.is_praise = item.is_praise?0:1
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.addPrase(item)
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
      lookTranslate(item){
        this.$router.push({
          path: '/publishPage',
          query: {
            information: item.activity_id,
            translate_id:item.translate_id
          }
        })
      },
      deleteTrans(item,index){
        this.$confirm('此操作将删除该翻译, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.deleteT(item,index)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      deleteT(item,index){
        this.$http.post(this.api + '/TranslateD',{
          token: localStorage.getItem('token'),
          translate_id: item.translate_id
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$message({
                type:'success',
                message: '删除成功!'
              })
              this.transList.splice(index,1)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.deleteT(item,index)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getOwer(){
        this.$http.post(this.api + '/home/User/get_user',{
          token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              this.owerId =res.data.data[0].user_id
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getOwer()
                })
            }else{
              alert(res.data.msg)
            }
          })
      },
    },
    mounted(){
        this.gettrans(this.languageId,1)
        this.getOwer()
    },
    created(){
        this.activeId = this.$route.query.activeId
        this.list = test()
    }
  }



